import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { Dialect } from 'app/core/models/dialect.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-dialect',
  templateUrl: './dialect.component.html',
  styleUrls: ['./dialect.component.scss']
})
export class DialectComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Dialect>;
  displayedColumns: string[] = ['dialect', 'status'];
  statusList = [...STATUSES];

  dialect = new Dialect();
  dialectList = [];

  dialectForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.dialects).subscribe((res: any) => {
      this.dialectList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Dialect>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void{
    this.apiService.save(ENDPOINTS.dialects, this.dialect).subscribe(
        (res: any) => {
          if (res !== undefined){
            this.sweetAlertService.success(res);
          }
        }, (err: any) => {
          this.sweetAlertService.error(err);
        }, () => {
          this.resetFormValidator(),
          this.getList();
        }
    )
  }

  getDialect(): void {
    this.apiService.findById(ENDPOINTS.dialects, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.dialect = res.responseData.data;
        console.log(res);

      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.dialects, this.dialect).subscribe(
      (res: any) => {
        if (res !== undefined){
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
      });
  }

  setFormValidator(): void {
    this.dialect.status = COMMON_FUNCTION.ACTIVE;
    this.dialectForm = this.formBuilder.group({
      dialect: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.dialect = new Dialect();
    this.dialect.status = COMMON_FUNCTION.ACTIVE;
    this.dialectForm.markAsUntouched();
    this.dialectForm.markAsPristine();
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this dialect?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.dialects, this.dialect.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Dialect successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
